/*CLOSED*/

.roll-link {
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60%;
    height: 75vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 1rem;
    margin: 1rem 0;
    transition: ease-in 0.2s;
    background-image: url('./media/0002.JPG');
}

.roll-link:hover {
    opacity: 0.6;
    cursor: pointer;
    width: 62%;
    height: 80vh;
}


.roll-title {
    color: black;
    width: 100%;
    text-align: center;
    z-index: 2;
}

.close-button {
    width: 10rem;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    cursor: pointer;
    transition: ease-in 0.2s;
}
.close-button:hover {
    opacity: 0.6;
    font-size: 1.1rem;
}

/*media queries*/
@media only screen and (max-width: 1100px) {
    .roll-link {
        width: 80%;
    }
    .roll-link:hover {
        width: 82%;
    }
}



/*OPEN*/
#grid-container-one {
    display: grid;
    grid-template: repeat(10, 1fr) / 1fr 1fr;
    width: 100%;
    height: auto;
}
.grid-image {
    padding: 2rem;
}
.grid-image img {
    width: 100%;
    height: auto;
    border-radius: 1rem;
}

#image-1 {
    grid-area: 0 / 0 / 1 / 1;
}
#image-2 {
    grid-area: 0 / 1 / 1 / 2;
}
#image-3 {
    grid-area: 1 / 0 / 2 / 1;
}
#image-4 {
    grid-area: 1 / 1 / 2 / 2;
}

/*OPEN media queries*/
@media only screen and (max-width: 900px) {
    #grid-container-one {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }
}